import styled from 'styled-components'
import React, { useState } from 'react';
import generate from './generation-logic/generate'

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-x: hidden;
  font-family: "Open Sans", sans-serif;
  height: 100%;
`

const Header = styled.div`
  background-color: rgb(4, 59, 116);
  color: white;
  min-height: 15vh;
  padding: 10px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  text-align: center;
`

const TopBar = styled.div`
    background-color: rgb(255, 198, 5);
    height: 40px;
    width: 100vw;
`

const StyledButton = styled.button`
   background-color: white;
   border: 3px solid;
   color: rgb(4, 59, 116);
   font-size: 20px;
   max-width: 200px;
   border-radius: 4px;
   min-width: 200px;
   margin: 20px;
   min-height: 40px;
   font-weight: bold;
   cursor: pointer;
   &:hover {
     background-color: rgb(4, 59, 116);
     color: white;
     border-radius: 10px;
   }
`

const SentencesContainer = styled.div`
   height: 100%;
   display: flex;
   flex-direction: column;
   align-items: flex-start;
`

const Sentence = styled.h3`
   font-weight: bold;
   margin: 10px;
`

const Footer = styled.div`
    margin-top: 1rem;
    padding: 0.3rem;
    background-color: white;
    padding-left: 1rem;
    width: 100%;
    font-size: 11px;
    font-style: italic;
    border-top: 1px solid;
    border-color: red;
    clear: both;
    position: relative;
    height: 20px;
    text-align: center;
`
const SubHeader = styled.p`
   display: block;
   @media (max-width: 768px) {
     display: none;
   }
`

function App() {
  const [sentences, setSentences] = useState([])

  return (
      <Container>
        <TopBar />
        <Header>
          <h1>Generator obostrzeń COVID-19</h1>
          <SubHeader>Sprawdź, co dzisiaj wolno, a czego nie</SubHeader>
        </Header>
        <StyledButton onClick={() => setSentences(generate())}>GENERUJ</StyledButton>
        <SentencesContainer>
          {sentences.map(sentence => (
            <Sentence>{sentence}</Sentence>
          ))}
        </SentencesContainer>
        <Footer>Ta strona to żart. Po prawdziwe informacje na temat obostrzeń udaj się <a href="https://www.gov.pl/web/koronawirus">tutaj</a></Footer>
      </Container>
  );
}

export default App;
